import { useEffect, useRef } from "react";
import Circle from "./figure/Circle";
import Triangle from "./figure/Triangle";
import Square from "./figure/Square";
import { Red, Yellow, Blue } from "./hook/Color.js";

import "./scss/intro.scss";

export default function Intro({ stageWidth, stageHeight }) {
  const titleRef = useRef(""),
    greetRef = useRef(""),
    personRef = useRef(""),
    greetEndRef = useRef("");
  let currentTitle = 0;
  useEffect(() => {
    greetRef.current.classList.add("active");
    animationEnd(greetRef, personRef);
    animationEnd(personRef, greetEndRef);
  }, []);
  return (
    <>
      <section id="intro">
        <div className="title" ref={titleRef}>
          <p className="greet" ref={greetRef}>
            안녕하세요. 반갑습니다.
          </p>
          <ul className="person" ref={personRef}>
            <li>
              <span>
                <b>뛰는 사람</b> 위의,
              </span>
            </li>
            <li>
              <span>
                <b>나는 사람</b> 위의,
              </span>
            </li>
            <li>
              <span>
                <b>즐기는 사람</b>.
              </span>
            </li>
          </ul>
          <p className="greetEnd" ref={greetEndRef}>
            최연수입니다.
          </p>
        </div>

        {/* <div className="figureBox">
          <Circle color={Red} />
          <Triangle color={Yellow} />
          <Square color={Blue} />
        </div> */}
      </section>
    </>
  );
}

const animationEnd = (current, next) => {
  current.current.addEventListener("animationend", () => {
    next.current.classList.add("active");
  });
};
